@use "../global/" as *;

// 画像パス
$imgPath : "images/";

// コンテナーの左右余白
$padding-side: 20px;
$padding-side-sp: 5%;

// ページの全体の横幅（大）
//$width_lg : 1080px;

// ページの全体の横幅（標準）
$width : 1084px;

// ページの全体の横幅（中間）
$width_md : 800px;

// ページの全体の横幅（狭い）
//$width_sm : 800px;

// レイアウト幅
$layout-width : $width + $padding-side * 2;

//標準フォントサイズ(sp)
//$fz_pc: 14px;

//標準フォントサイズ(pc)
