// $c_gray3: #707070;
// $c_gray4: #E8E8E9;
// $c_gray5: #505050;

//キーカラー
$c_key1: #CEB967;
$c_key2: #F5EFD7;
$c_key3: #A88E25;

//黄色系
// $c_yellow1: #FFD800;

//青系
// $c_blue1: #1286EA;
