// 標準テキストカラー
$c_text: #341E12;

// リンクカラー
$c_link: $c_text;
$c_hover: $c_key3;
$c_active: $c_key3;

// z-index
$layer: (
  modal: 100,
  header: 20,
  tooltip: 10,
  badge: 2,
  default: 1,
);

@mixin z-index($key) {
  z-index: map-get($layer, $key);
}